@import url("https://fonts.googleapis.com/css2?family=Helvetica:wght@300&family=IBM+Plex+Mono:wght@500&display=swap");

body {
  background-color: #8d99ae;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: "Helvetica", sans-serif;
  margin: 0;
}

.frame {
  width: 800px;
  height: 600px;
  background-color: #edf2f4;
  border-radius: 25px;
  box-shadow: 14px 15px 44px rgba(0, 0, 0, 0.1),
              55px 59px 81px rgba(0, 0, 0, 0.09),
              123px 134px 109px rgba(0, 0, 0, 0.05),
              219px 238px 129px rgba(0, 0, 0, 0.01),
              342px 371px 141px rgba(0, 0, 0, 0);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}

.header {
  background-color: #2b2d42;
  height: 44px;
  border-radius: 25px 25px 0 0;
}

.table-container {
  flex-grow: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

table {
  width: 95%;
  margin: 0 auto;
  border-collapse: separate;
  border-spacing: 0;
  border-radius: 10px;
  border: 2px solid #343a40;
}

th,
td {
  border: 1px solid #343a40;
  padding: 0;
  text-align: center;
  height: 30px;
}

th {
  background-color: #bfc9da;
  width: 150px;
}

td {
  background-color: #edf2f4;
}

input[type="text"],
select {
  width: 100%;
  height: 100%;
  border: none;
  text-align: center;
  padding: 5px;
  font-size: 16px;
  background-color: transparent;
  font-family: "Helvetica", sans-serif;
  outline: none; 
}

.button-row {
  display: flex;
  justify-content: space-between;
  padding: 0 60px;
  transform: translateY(-50px);
}

.button-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 60px;
}

.button {
  width: 300px;
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 16px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.btn-green {
  color: #228b22;
  border: 2px solid #228b22;
}

.btn-green:hover {
  background-color: #247e24;
  color: white;
}

.btn-red {
  color: #780000;
  border: 2px solid #ba2d2d;
}

.btn-red:hover {
  background-color: #ba2d2d;
  color: white;
}

.button-container_2 {
    display: flex;
    justify-content: space-between;
    padding: 0 40px;
    position: absolute;
    bottom: 5px;
    width: 100%;
  }
  
  .btn-back,
  .btn-next {
    background-color: #2b2d42;
    color: white;
    outline: none;
    border: none;
    width: 220px;
    height: 50px;
    border-radius: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: 23px;
    font-family: "IBM Plex Mono", monospace;
    box-shadow: 2px 2px 7px rgba(13, 13, 13, 0.29),
                9px 9px 13px rgba(13, 13, 13, 0.26),
                20px 21px 17px rgba(13, 13, 13, 0.15),
                35px 37px 20px rgba(13, 13, 13, 0.04),
                55px 58px 22px rgba(13, 13, 13, 0.01);
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .btn-back:hover,
  .btn-next:hover {
    background-color: #495057;
  }
  
  .btn-back i {
    margin-right: 10px;
  }
  
  .btn-next i {
    margin-left: 10px;
  }
  

.btn-back span,
.btn-next span {
  transform: translateY(-2px);
}

.selected {
  background-color: #ffe6e6;
}

.selected-table {
  border: 2px solid #228b22; 
}

.input-error {
    border: 2px solid red;
    background-color: #ffe6e6;
  }
  
  .error-popup {
    position: fixed; 
    top: 10%; 
    left: 30%;
    transform: translateX(-50%);
    background-color: #ffcccc;
    color: #990000;
    border: 2px solid #ff0000;
    border-radius: 15px;
    padding: 10px 20px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
    font-family: "IBM Plex Mono", monospace;
    font-size: 18px;
    text-align: center;
    animation: fadeInOut 3s forwards;
    z-index: 10;
    width: 41%;
  }
  
  @keyframes fadeInOut {
    0% {
      opacity: 0;
      transform: translateY(-10px);
    }
    10% {
      opacity: 1;
      transform: translateY(0);
    }
    90% {
      opacity: 1;
      transform: translateY(0);
    }
    100% {
      opacity: 0;
      transform: translateY(-10px);
    }
  }
  


